html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Josefin Sans', sans-serif;
  color: #fff; }

.portfolio-background {
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background-position: center top;
  background-size: cover; }

section {
  padding: 30px 0; }

.section-title {
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 30px; }
  @media (min-width: 768px) {
    .section-title {
      font-size: 42px; } }
  @media (min-width: 992px) {
    .section-title {
      font-size: 48px; } }

@media (min-width: 768px) {
  .section-subtitle {
    font-size: 32px; } }

@media (min-width: 992px) {
  .section-subtitle {
    font-size: 38px; } }

.form {
  padding-top: 30px; }
  .form-control {
    height: 34px;
    border-radius: 0; }
  .form label {
    display: block; }
  .form .input-group {
    position: relative; }
  .form .input-addon {
    width: 40px;
    height: 34px;
    line-height: 34px;
    background: #ccc;
    color: #fff;
    display: inline-block;
    text-align: center;
    position: absolute;
    bottom: 0;
    right: 0; }
  .form .icon-row {
    text-align: right; }
    .form .icon-row span {
      border: 1px solid #ccc;
      border-radius: 2px;
      padding: 8px; }
      .form .icon-row span:hover {
        cursor: pointer; }
      .form .icon-row span:not(:last-child) {
        margin-right: 5px; }

.tabs {
  padding: 0;
  margin: 0;
  list-style: none; }
  .tabs .tab {
    border-left: 1px solid #ccc;
    border-top: 1px solid #ccc;
    padding: 8px 15px;
    margin: 0;
    float: left; }
    .tabs .tab:last-child {
      border-right: 1px solid #ccc; }
    .tabs .tab:hover {
      cursor: pointer; }

/*#region vertical gutters*/
.no-gutter-top, .no-gutter {
  margin-top: 0 !important; }

.no-gutter-bottom, .no-gutter {
  margin-bottom: 0 !important; }

.gutter-top-sm, .gutter-sm {
  margin-top: 5px !important; }

.gutter-bottom-sm, .gutter-sm {
  margin-bottom: 5px !important; }

.gutter-top, .gutter {
  margin-top: 15px !important; }

.gutter-bottom, .gutter {
  margin-bottom: 15px !important; }

.gutter-top-lg, .gutter-lg {
  margin-top: 30px; }

.gutter-bottom-lg, .gutter-lg {
  margin-bottom: 30px; }

.gutter-top-xl, .gutter-xl {
  margin-top: 30px; }
  @media (min-width: 768px) {
    .gutter-top-xl, .gutter-xl {
      margin-top: 60px; } }

.gutter-bottom-xl, .gutter-xl {
  margin-bottom: 30px; }
  @media (min-width: 768px) {
    .gutter-bottom-xl, .gutter-xl {
      margin-bottom: 60px; } }

/*#endregion*/
/*#region horizontal gutters*/
.gutter-left-sm {
  margin-left: 5px; }

.gutter-right-sm {
  margin-right: 5px; }

.gutter-left {
  margin-left: 15px; }

.gutter-right {
  margin-right: 15px; }

.gutter-left-auto {
  margin-left: auto; }

.gutter-right-auto {
  margin-left: auto; }

.gutter-left-lg {
  margin-left: 30px; }

.gutter-right-lg {
  margin-right: 30px; }

/*#endregion*/
.btn-partial {
  position: relative;
  color: #fff; }
  .btn-partial:before, .btn-partial:after {
    content: '';
    display: block;
    position: absolute;
    width: 45px;
    height: 25px;
    transition: .3s width, .3s height; }
  .btn-partial:before {
    top: 0;
    left: 0;
    border-top: 3px solid #fff;
    border-left: 3px solid #fff; }
  .btn-partial:after {
    bottom: 0;
    right: 0;
    border-bottom: 3px solid #fff;
    border-right: 3px solid #fff; }
  .btn-partial:hover {
    color: #fff; }
    .btn-partial:hover:before, .btn-partial:hover:after {
      width: 100%;
      height: 100%; }

.btn-fat {
  padding: 12px 20px; }

.about {
  background: rgba(149, 160, 183, 0.8); }
  .about .social img {
    max-width: 50px; }

.banner .logo {
  max-width: 120px; }

@media (min-width: 768px) {
  .banner h1 {
    font-size: 50px; }
  .banner h3 {
    font-size: 28px; } }

@media (min-width: 992px) {
  .banner h1 {
    font-size: 64px; }
  .banner h3 {
    font-size: 36px; } }

.portfolio-list {
  position: relative;
  background: rgba(69, 87, 125, 0.6); }
  .portfolio-list .portfolio-item {
    background: #FFEDCA;
    position: relative;
    width: 100%;
    max-width: 500px;
    white-space: normal; }
    .portfolio-list .portfolio-item a {
      color: #fff; }
      .portfolio-list .portfolio-item a img {
        opacity: .7;
        transition: .3s opacity; }
      .portfolio-list .portfolio-item a:hover {
        color: #fff; }
        .portfolio-list .portfolio-item a:hover img {
          opacity: 1; }
    .portfolio-list .portfolio-item-title {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      margin: 0;
      padding: 15px 0;
      background-color: #BB9B5E; }
    @media (min-width: 480px) and (max-width: 991px) {
      .portfolio-list .portfolio-item {
        width: calc(50% - 30px / 2); } }
    @media (min-width: 992px) {
      .portfolio-list .portfolio-item {
        width: calc(33.33% - 30px * 2 / 3); } }
